.post-container {
 font-family: $post-font-family;
 font: $post-font;
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
 padding: $navbar-expanded-height 2rem;
 max-width: 800px;
 margin-left: auto;
 margin-right: auto;

 .post-header {
 @include relative-font-size(0.9);
 margin-bottom: 3rem;

 .post-meta {
 color: var(--main-text-light-color);
 }
 }

 .icon-avatar {
 background-color: transparent;
 border: none;
 border-radius: 50%;
 }

 .post-datetime {
 padding-left: 4px;
 }

 .tag {
 font-size: 0.7rem;
 font-weight: 500;
 padding: 0.3rem 0.7rem;
 margin-right: 0.5rem;
 border: none;
 border-radius: 5px;
 color: var(--main-text-light-color);
 background-color: var(--tag-background-color);

 &:hover,
 &:active,
 &:focus {
 text-decoration: none;
 color: var(--main-text-color);
 background-color: var(--tag-background-hover-color);
 }
 }

 h2 {
 margin: 4rem 0 2rem;
 border-bottom: 1px solid var(--invert-text-color);
 font-size: 2rem;
 font-weight: 600;
 }

 h3,
 h4,
 .post-title {
 font-weight: 700;
 margin: 2.5rem 0 1.5rem;
 }

 h3,
 h4 {
 color: var(--main-text-light-color);
 }

 .post-body {
 @include relative-font-size(1.2);
 }
}

// Syntax highlight
/* for block of numbers */
.hljs-ln-numbers {
 -webkit-touch-callout: none;
 -webkit-user-select: none;
 -khtml-user-select: none;
 -moz-user-select: none;
 -ms-user-select: none;
 user-select: none;

 text-align: center;
 color: #ccc;
 border-right: 1px solid #ccc;
 vertical-align: top;
 padding-right: 5px;

 /* your custom style here */
}

/* for block of code */
.hljs-ln-code {
 padding-left: 10px;
}

.hljs-ln td {
 padding-right: 5px !important;
 padding-left: 5px !important;
}

.hl-line {
 // background: #545b62;
 background: rgba(255, 255, 255, 0.2);
}

table:not(.hljs-ln) {
 font-family: open sans, -apple-system, BlinkMacSystemFont, segoe ui, Roboto, helvetica neue, Arial, sans-serif,
 apple color emoji, segoe ui emoji, segoe ui symbol;
 margin: 25px auto;
 border-collapse: collapse;
 box-shadow: 3px 5px 20px var(--table-box-shadow-color);
 border-right: none;
 border-left: none;
 th,
 td {
 padding: 8px 5px;
 border-collapse: collapse;
 }
 td {
 font-size: 13px;
 color: var(--main-text-light-color);
 &.last {
 border-right: none;
 }
 &.first {
 border-left: none;
 }
 }
 th {
 border-bottom: 1px solid rgba(0, 0, 0, 0.3);
 background: var(--table-header-background-color);
 color: var(--main-text-color);
 font-size: 15px;
 font-weight: 700;
 }

 tbody tr:nth-child(odd) {
 background: var(--table-odd-row-background-color);
 }
 tbody tr:nth-child(even) {
 background: var(--table-even-row-background-color);
 }

 .rect {
 border-top: 3px solid #333;
 border-bottom: 3px solid #333;
 padding-top: 15px;
 margin: 0 auto;
 }

 .rel {
 width: 200px;
 display: inline-block;
 padding: 5px;
 opacity: 0.8;
 }

 .rel hover {
 opacity: 1;
 }
}
